.settings {
  padding: 1em;
  z-index: 100;
  position: absolute;
  top: 43px;
  left: 10px;
  width: 170px;
}

  .settings li {
    list-style: none;
  }

  .settings li a {
      display: block;
      width: 100%;
      box-sizing: border-box;
    }

  .settings > a {
    margin: 5px;
    display: inline-block;
  }

  .settings h4 {
    font-weight: bold;
    padding-left: 7px;
    padding-top: 4px;
    padding-bottom: 4px;
  }

  .settings h4:first-child {
      padding-top: 0;
    }

.settings:after {
  left: 75px;
}
